.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
}

.launch-data-container {
  display: flex;
  flex-direction: column;
  flex: 1 1 0;
  justify-content: center;
  align-items: flex-start;
  gap: 2rem;
  width: 75vw;
}
.launch-data-container input {
  width: 75vw;
  padding: 0.5rem;
}
.launch-data-container .launch-data {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.launch-data-container .launch-data img {
  width: 8rem;
}

